import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { useHistory } from "react-router-dom";
import '../../styles/Sell.css';
import Footer from '../Comps/Footer';


function Sell() {
  let history = useHistory();
  const initialValues = {
    firstname: "",
    lastname: "",
    email: "",
    companyname: "",
    companywebsite: "",
    companytitle: "",
    phonenumber: "",
    revenue: "",
    employees: "",
    products: "",
    description: "",

  };

  const validationSchema = Yup.object().shape({
    firstname: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    lastname: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    email: Yup.string().min(3).max(30).required("Veuillez remplir ce champ obligatoire.").email("Email invalide"),
    companyname: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    companywebsite: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    companytitle: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    phonenumber: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    revenue: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    employees: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    products: Yup.string().required("Veuillez remplir ce champ obligatoire."),
    description: Yup.string().required("Veuillez remplir ce champ obligatoire."),
  });

  const onSubmit = (data) => {
    axios.post("http://localhost:3001/dems/dem", data).then((response) => {
      if (response.data.error) {
        alert(response.data.error);
      } else {
        alert('Demande soumise avec succs');
      history.push("/");
    }
    });
  };

  return (

    <>

<div className='sell-container'>
      

        
      <Formik
        initialValues={initialValues}
        onSubmit={onSubmit}
        validationSchema={validationSchema}
      >
        <Form className="formContainer2">


        <section className='sell-subscription'>
        <p className='sell-subscription-text'>
        Vendre sur <p className='sell-subscription-heading'> SHOPYSOLDE </p>
        </p>
        <p className='sell-subscription-text'>
        Pour postuler et devenir partenaire ou pour obtenir plus d'informations sur la vente avec SHOPYSOLDE
         veuillez rpondre  ces questions rapides sur la socit et les produits et un responsable l'examinera et vous contactera sous peu.
        </p>
        </section>



          <label>Prnom : </label>
          <div style={{ color: 'red'}}>
          <ErrorMessage name="firstname" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="firstname"

          />


          <label>Nom : </label>
          <div style={{ color: 'red'}}>
          <ErrorMessage name="lastname" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="lastname"

          />

          
          <label>E-mail : </label>
          <div style={{ color: 'red'}}>
          <ErrorMessage name="email" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="email"

          />

<label>Nom de l'entreprise : </label>
<div style={{ color: 'red'}}>
          <ErrorMessage name="companyname" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="companyname"

          />
  
  <label>Titre dans l'entreprise : </label>
  <div style={{ color: 'red'}}>
          <ErrorMessage name="companytitle" component="span" />
          </div>
          <Field component="select" name="companytitle" id="inputCreateDem" >
          <option value="" label="Veuillez slectionner" />
          <option value="Propritaire / Partenaire">Propritaire / Partenaire</option>
          <option value="PDG / Prsident">PDG / Prsident</option>
          <option value="Directeur financier / vice-prsident des finances">Directeur financier / vice-prsident des finances</option>
          <option value="Vice-prsident marketing">Vice-prsident marketing</option>
          <option value="Vice-prsident Oprations">Vice-prsident Oprations</option>
          <option value="Directeur d'entrept">Directeur d'entrept</option>
          <option value="Autre">Autre</option>
          </Field>
 
<label>Site Web d'entreprise : </label>
          <div style={{ color: 'red'}}>
          <ErrorMessage name="companywebsite" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="companywebsite"

          />

<label>Numro de tlphone : </label>
          <div style={{ color: 'red'}}>
          <ErrorMessage name="phonenumber" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="phonenumber"

          />

<label>Revenu annuel estim de l'entreprise  : </label>
<div style={{ color: 'red'}}>
          <ErrorMessage name="revenue" component="span" />
          </div>
          <Field component="select" name="revenue" id="inputCreateDem" >
          <option value="" label="Veuillez slectionner" />
          <option value="Moins de $1 million">Moins de $1 million</option>
          <option value="$1 million  $10 millions">$1 million  $10 millions</option>
          </Field>


<label>Nombre d'employs de l'entreprise  : </label>
<div style={{ color: 'red'}}>
          <ErrorMessage name="employees" component="span" />
          </div>
          <Field component="select" name="employees" id="inputCreateDem" >
          <option value="" label="Veuillez slectionner" />
          <option value="Moins de 10 employs">Moins de 10 employs</option>
          <option value="10  100 employs">10  100 employs</option>
          <option value="Plus de 100 employs">Plus de 100 employs</option>



          </Field>

<label> quelle frquence aurez-vous des produits  liquider?  : </label>
<div style={{ color: 'red'}}>
          <ErrorMessage name="products" component="span" />
          </div>
          <Field component="select" name="products" id="inputCreateDem" >
          <option value="" label="Veuillez slectionner" />
          <option value="Une seule liquidation (une seule fois)">Une seule liquidation (une seule fois)</option>
          <option value="Liquidations quotidiennes / hebdomadaires.">Liquidations quotidiennes / hebdomadaires.</option>
          <option value="Liquidations mensuelles">Liquidations mensuelles</option>
          <option value="Liquidations trimestrielles / annuelles.">Liquidations trimestrielles / annuelles.</option>

          </Field>

<label>Dcrivez brivement les marques / types de produit (s) dont vous avez besoin pour liquider : </label>
          <div style={{ color: 'red'}}>
          <ErrorMessage name="description" component="span" />
          </div>
          <Field
            autocomplete="off"
            id="inputCreateDem"
            name="description"
            placeholder="20 caractres minimum. 200 caractres maximum"

          />



          <button type="submit">SOUMETTRE UNE DEMANDE</button>
        </Form>
      </Formik>
    </div>
    <Footer />
  </>
  );
}


export default Sell;